  <template>
    <div class="reg" @keyup.enter="reg">
      <el-form class="reg-form">
        <h1 class="title">样板管理系统</h1>
        <el-form-item>
          <el-input
            :autofocus="true"
            placeholder="请输入账号"
            v-model="username">
            <template slot="prepend"><i class="el-icon-mobile-phone"></i></template>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-input
            placeholder="请输入密码"
            type="password"
            v-model="password">
            <template slot="prepend"><i class="el-icon-info"></i></template>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-input
            placeholder="请再次输入密码"
            type="password"
            v-model="repassword">
            <template slot="prepend"><i class="el-icon-info"></i></template>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-button style="width:100%" @click.native="reg" type="primary" :loading="isBtnLoading">{{btnText}}</el-button>
        </el-form-item>
      </el-form>
      <a class="login" @click="toLogin"> 跳转登录页面 </a>
    </div>
  </template>

<script>
  import api from 'api/user'
  import axios from 'axios'
  import * as util from 'assets/js/util'
  
  export default {
    name: "",
    data() {
      return {
        username: '',
        password: '',
        repassword: '',
        isBtnLoading: false
      }
    },
    computed: {
      btnText() {
        if (this.isBtnLoading) {
          return '注册中...'
        }
        return '注册'
      }
    },
    methods: {
      toLogin() {
        this.$router.push('/login')
      },
      reg() {
        if (!(this.username.trim())) {
          this.$message.error('请填写用户名')
          return
        }
        if (!this.password) {
          this.$message.error('请填写密码')
          return
        }
        if (!this.repassword) {
          this.$message.error('请再次填写密码')
          return
        }
        if (this.repassword !== this.password) {
          this.$message.error('两次密码不一致')
          return
        }
        let regParams = {
          userName: this.username,
          userPassword: this.password
        }
        this.isBtnLoading = true
        var t = JSON.stringify(regParams)
        // console.log(t)
        // axios.post('http://10.108.171.160:80/login/register', regParams)
        api.Reg(t).then(res => {
          console.log(res)
          this.isBtnLoading = false
          if (res.data.success === "SUCCESS") {
            this.$message.success("注册成功")
            this.username = ''
            this.password = ''
            this.repassword = ''
            let obj = {
              token: res.data.data
            }
            util.handleSession('token', obj)
            this.$emit('login', '/')
          } else {
            this.$message.error(res.data.data)
          }
        }).catch(err => {
          this.$message.error("注册失败，服务器异常")
          console.log(err)
        })
      }
    },
    created() {
      sessionStorage.clear()
    }
  }
</script>

<style scoped>
  .reg-form {
    width: 400px;
    margin: 13% auto 0;
  }
  .title {
    margin-bottom: 30px
  }
  @media screen and (max-width: 767px) {
    .reg-form {
      width: 80%;
      margin: 20% auto 0;
    }
  }
</style>



